.friends {
  padding-top: 80px;
  padding-bottom: 100px;
  background-color: $color-light-l;
}

.friends__title {
  margin: 0 auto 58px;
  width: fit-content;
  text-align: center;
}

.friends__list {
  @include list-reset;

  display: flex;
  gap: 90px;
  justify-content: center;

  margin-bottom: 61px;

  &--catalog {
    flex-wrap: wrap;
    gap: 30px 40px;
    // padding-bottom: 15px;
  }
}

.friends__item {
  background-color: $color-light-s;
  border-radius: 9px;
  padding-bottom: 30px;

  &:hover {
    cursor: pointer;
    background-color: $color-white;

    :not(:disabled).friends__button {
      background-color: $color-primary-light;
      border-color: $color-primary-light;
    }
  }
}

.friends__title-card {
  text-align: center;
  margin-bottom: 30px;
}

.friends__img {
  display: block;
  margin-bottom: 31px;
}

.friends__button {
  display: block;
  margin: 0 auto;
  width: fit-content;
  text-decoration: none;
}
