@mixin font ($family: $font-base, $size, $line-height, $weight: 400, $color, $style: normal) {

  margin-top: 0;
  margin-bottom: 0;

  font-family: $family;
  font-size: $size;
  line-height: $line-height;
  font-weight: $weight;
  color: $color;
  font-style: $style;
}

@mixin list-reset ($padd-left: 0) {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: $padd-left;
  list-style: none;
}

@mixin media-desktop {
  @media (min-width: $desktop-width) {
    @content;
  }
}

@mixin media-tablet {
  @media (min-width: $tablet-width) {
    @content;
  }
}

@mixin media-mobile {
  @media (min-width: $mobile-width) {
    @content;
  }
}


